@import '../../variables.scss';

.sup-buttons {
    position: absolute;
    top: 18px;
    right: 32px;
    padding: 15px 15px 13px 13px;
    border-radius: 100%;
    background-color: $footer-color;
}

.cart {
    position: relative;
    &__index {
        position: absolute;
        top: -7px;
        right: -7px;
        border-radius: 100%;
        width: 18px;
        height: 18px;
        background-color: $main-color;
        text-align: center;
        padding: 0;
    
        font-family: "Poppins", sans-serif;
        font-weight: 400;
        font-size: 13px;
        color: $font-color;
    }
}